@import '~sweetalert2/dist/sweetalert2.css';

.emap-input-file-hidden {
    display: none;
}

.emap-device-list-binding-license-count {
    color: #474e5a80;
    margin-top: 20px;
    margin-right: 10px;
    text-align: right;
}

.emap-dialog-popup {
    width: 736px;

    .emap-dialog-header {
        margin-top: 60px;
        padding-left: 56px;

        .emap-dialog-title {
            width: 310px;
            font-size: 28px;
            text-align: left;
            color: black;
            margin-left: 1.5rem;
        }
    }

    .emap-dialog-content {
        margin-top: 24px;
        margin-bottom: 24px;
        padding-left: 56px;
        font-size: 14px;
        text-align: left;
        margin-left: 1.5rem;
    }
}

.emap-upload-image-button {
    background-color: #f00;
}

.emap-card {
    font-family: 'Century Gothic';
    background-color: #fff;
    margin: 10px;
    padding: 24px;

    img.drop-target,
    &.drop-target {
        border: 3px dashed #67809f;
    }

    .emap-text {
        color: #272727;

        &.emap-title {
            font-weight: Bold;
            font-size: 28px;
        }

        &.emap-content {
            font-size: 16px;
        }

        &.emap-center {
            text-align: center;
        }

        &.preview-source-title {
            font-size: 45px;
        }

        &.preview-track-title {
            font-size: 36px;
        }

        &.preview-content {
            margin-top: 25px;
        }

        &.preview-count {
            margin-top: 5px;
            font-weight: Bold;
            font-size: 28px;
        }

        &.preview-source {
            padding-left: 15px;
        }

        &.modify-title {
            font-size: 28px;
        }

        &.device-info-title {
            font-size: 28px;
        }

        &.track-subtitle {
            font-size: 14px;
            color: #afafaf;
        }

        &.track-count-text {
            font-size: 14px;
            color: #272727;
            .fas.fa-angle-left.source-list-dropdown {
                float: right;
                padding: 3px 10px;
                font-size: 18px;
                &.active {
                    transform: rotate(-90deg);
                }
            }
        }

        &.emap-binding-list-title {
            margin-top: 28px;
            color: #474e5a66;
            font-size: 16px;
        }
    }

    ///////// Preview /////////

    .preview-image-div {
        margin-left: auto;
        margin-bottom: auto;
        margin-top: auto;
        margin-bottom: auto;
        text-align: center;

        .preview-image {
            max-width: 100%;
        }
    }

    .preview-source-div {
        margin-top: 10px;
    }

    .preview-button-div {
        width: 100%;
        padding-right: 30px;
        margin-top: 55px;

        .preview-button {
            width: 100%;
        }
    }

    ///////// Modify Image /////////
    .modify-image-div {
        position: relative;
        margin-left: auto;
        margin-right: auto;

        &.image-empty {
            max-width: 335px;
        }

        &.image-set {
            text-align: center;
        }

        .modify-image {
            width: 100%;
            user-select: none;
        }

        .modify-source-div {
            position: absolute;
            left: 0;
            top: 0;
            z-index: 300;
            cursor: pointer;

            &.selection {
                z-index: 1000;
            }

            .modify-icon-div {
                position: relative;
                z-index: 300;
                left: 0;
                top: 0;
                width: 42px;
                height: 42px;
                background-color: #2e75b6;
                border-radius: 50%;

                &.selection {
                    background-color: #0197a8;
                }

                &.no-track {
                    background-color: #707070;
                }

                .modify-icon {
                    width: 42px;
                    height: 42px;
                }
            }

            .modify-device-light {
                &.normal {
                    width: 0;
                    height: 0;
                    border-style: solid;
                    border-width: 0 20px 60px 20px;
                    border-color: transparent transparent #fff23e99 transparent;
                    position: absolute;
                    z-index: 3000;
                    top: 21px;
                    left: 0px;
                    transform-origin: 21px 0px;
                }

                &.fisheye,
                &.video {
                    width: 160px;
                    height: 160px;
                    position: absolute;
                    z-index: 100;
                    left: -60px;
                    top: -60px;
                    border-radius: 50%;
                    background-color: #fff23e99;
                }

                &.event {
                    width: 160px;
                    height: 160px;
                    position: absolute;
                    z-index: 100;
                    left: -60px;
                    top: -60px;
                    border-radius: 50%;
                    background-color: #c2fdc199;
                }

                &.track {
                    width: 160px;
                    height: 160px;
                    position: absolute;
                    z-index: 100;
                    left: -60px;
                    top: -60px;
                    border-radius: 50%;
                    background-color: #d2f6f999;
                }
            }

            .modify-index-label {
                position: relative;
                z-index: 200;
                color: #fff;
                background-color: #000;
                padding: 3px 0px;
                margin-top: 3px;
            }
        }
    }

    .modify-content-div {
        margin-top: 25px;
    }

    .modify-button-div {
        margin-top: 25px;

        .modify-button {
            width: 100%;
        }
    }

    .modify-button-remove {
        width: 25px;
        height: 25px;
        position: absolute;
        top: 24px;
        right: 40px;
        z-index: 100;
        background-color: #3c515a;
        color: #ffffff;
        text-align: center;
        font-size: 16px;
        border-radius: 56%;
        cursor: pointer;
    }

    ///////// Modify Device Information /////////

    .device-info-content-div {
        margin-top: 24px;

        .device-info-row {
            margin-bottom: 24px;

            .device-info-label {
                font-size: 16px;
            }

            .device-info-text {
                font-weight: bold;
                font-size: 16px;

                &.disable {
                    color: #aeaeae;
                }
            }
        }
    }

    .device-info-fisheye-switch {
        display: inline-flex;
        flex: 1;
        margin-left: 10px;
        margin-right: 0;
    }

    .device-info-input-orientation {
        width: 75%;
        display: inline-flex;
        flex: 1;
        margin-left: 0;
        margin-right: 0;
    }

    .device-info-orientation-text {
        font-size: 16px;

        &.min {
            text-align: left;
            width: 10%;
            float: left;
        }

        &.max {
            text-align: right;
            width: 15%;
            float: right;
        }
    }

    .device-info-input-orientation {
        width: 75%;
        display: inline-flex;
        flex: 1;
        margin-left: 0;
        margin-right: 0;
    }

    ///////// Modify Device List /////////

    .device-list-type-group {
        width: 100%;
        margin-top: 20px;
        user-select: none;

        &.emap-device-list-binding-button-group {
            box-shadow: 3px 3px 3px #cccccc55;
            border-radius: 0.25rem;

            .emap-device-list-binding-button {
                &.btn-primary {
                    border: none;
                    background-color: #bac8d2;
                    color: #474e5a;

                    &:hover {
                        background-color: #bac8d2;
                    }
                }

                &.btn-outline-primary {
                    border: none;
                    color: #474e5a;

                    &:hover {
                        background-color: #c6d2da;
                    }
                }
            }
        }
    }

    .emap-device-list-div {
        margin-top: 10px;
        user-select: none;

        .emap-device-list-server-name {
            margin-top: 5px;
            font-weight: bold;
            font-size: 18px;
            user-select: none;

            .emap-device-list-binding-select-text {
                padding-left: 5px;
                color: #474e5a80;
            }

            .floor-emap-source-list-dropdown {
                float: right;
                padding: 3px 10px;
                &.active {
                    transform: rotate(-90deg);
                }
            }
        }

        .emap-device-list-source-name {
            margin-top: 3px;
            padding: 5px;
            padding-left: 10px;
            font-size: 16px;
            user-select: none;
            cursor: pointer;

            &.active {
                color: #afafaf;
            }

            &.track {
                color: #272727;
            }

            &.no-track {
                color: #afafaf;
            }

            &.selection {
                background-color: #e8e8e8;
            }
        }
    }

    ///////// Binding Image Box /////////

    &.emap-card-binding {
        background-color: #ffffff00;
        padding: 0;
        margin: 0;
    }

    .emap-binding-building {
        .emap-binding-building-name {
            color: #474e5a;
        }
        .emap-binding-building-address {
            color: #474e5a80;
            margin-bottom: 60px;
        }
    }

    .emap-binding-empty {
        text-align: center;

        .emap-binding-empty-image {
            max-width: 350px;
            margin-top: 40px;
        }

        .emap-binding-empty-title {
            max-width: 350px;
            margin-top: 35px;
            margin-left: auto;
            margin-right: auto;
            text-align: center;
            font-weight: bold;
            font-size: 24px;
            color: #474e5a;
        }
    }
}
