$headerUserCardWidth: 250px;

header.header {
    width: $widthHeader;
    height: $heightHeader;
    position: fixed;
    top: 0px;
    left: 0px;
    background: $colorWhite 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 10px #00000029;
    z-index: 5;
    padding-top: 0.7rem;

    .header-hamburger {
        user-select: none;
        cursor: pointer;
    }

    .header-logo-project-name {
        font-weight: Bold;
        font-size: 1.2rem;
        color: $colorTextDark;
        display: inline;
        user-select: none;
        cursor: pointer;
    }

    .header-logo-image {
        height: 3.6rem;
        padding: 10px 10px 10px 0;
        user-select: none;
        cursor: pointer;
        position: relative;
    }

    .row {
        padding: 0 30px 0 0;

        .col {
            .systemName {
                margin: 12px;
                text-align: left;
                font-weight: Bold;
                letter-spacing: 0.35px;
                color: $colorTextDark;
                opacity: 1;
            }
        }

        .user-avatar-wrap {
            float: right;
            line-height: 58px;
            margin: 0 10px;
        }

        .user-name-dash {
            float: right;
            line-height: 50px;
            margin: 0 1.2rem;
            font-size: 1.5rem;
        }

        .user-card-wrap {
            line-height: 20px;

            .trigger-user-info {
                float: right;
                outline: none;
                height: 60px;
                border: none;
                background: none;
                font-weight: Bold;
                letter-spacing: 0.14px;
                opacity: 1;
                outline: none;
                padding: 0 12px;
            }
        }

        .user-language-wrap {
            float: right;
            height: 60px;
            padding: 12px 0;

            .multiselect.user-info-language {
                width: 160px;
            }
        }

        .user-message-wrap {
            float: right;
            line-height: 60px;
            margin: 0 10px;

            .trigger-user-message {
                font-size: 1.5rem;

                .badge {
                    height: 10px;
                    width: 10px;
                    background-color: $colorRed;
                    border-radius: 50%;
                    display: inline-block;
                    position: relative;
                    top: -5px;
                    right: 6px;
                }
            }
        }
    }
}

.tippy-tooltip.light-theme {
    background: $colorWhite;

    .user-info-card {
        .card-body {
            min-width: 200px;
            padding: 20px;
            position: relative;
            border: none;

            .b-avatar.user-avatar.badge-secondary.rounded-circle {
                margin-top: 6px;
            }

            .user-name-text,
            .user-message-text,
            .more-btn-card {
                font-weight: Bold;
                letter-spacing: 0.14px;
                opacity: 1;
                margin: 10px 0 0 0;
                color: #000;

                .message-text {
                    margin: 5px 0;

                    & > i {
                        margin-right: 10px;
                    }
                }
            }

            .user-account-text {
                top: 92px;
                font-size: 10px;
                letter-spacing: 0.12px;
                opacity: 1;
                color: #000;
            }

            .user-myprofile-text {
                text-align: left;
                font-size: 12px;
                letter-spacing: 0.12px;
                opacity: 1;
                margin: 10px 0;
                cursor: pointer;

                .vue-js-switch {
                    position: absolute;
                    right: 22px;
                }
            }

            .user-myprofile-button {
                color: #000;
                font-size: 12px;
                padding: 0;
                text-align: left;
            }

            .user-skin-text {
                text-align: left;
                font-size: 12px;
                letter-spacing: 0.12px;
                opacity: 1;
                margin: 10px 0;

                .vue-js-switch {
                    position: absolute;
                    right: 22px;
                }
            }

            .user-myprofile-wrap {
                margin-top: 38px;
                color: #000;
            }
        }

        .user-logout {
            margin-top: 30px;

            .user-logout-button {
                width: 100%;
            }
        }
    }
}
