.toasted-container {
    .toasted.method-success {
        background-color: rgba(102, 128, 159, 0.5);
        box-shadow: 0px 3px 6px #00000029;
        border-radius: 0.5rem;
        color: $colorWhite;
        padding: 1.2rem;
        padding-left: 1.5rem;
        font-size: 1.2rem;
        font-weight: bold;
    }

    .toasted.method-error {
        background-color: rgba(193, 67, 67, 0.5);
        box-shadow: 0px 3px 6px #00000029;
        border-radius: 0.5rem;
        color: $colorWhite;
        padding-top: 1.2rem;
        padding-bottom: 1.2rem;
        padding-left: 1.5rem;
        font-size: 1.2rem;
        font-weight: bold;
    }
}
