nav.nav {
    position: fixed;
    width: $widthNav;
    top: $heightHeader;
    bottom: -50px;
    height: auto;

    .v-sidebar-menu.vsm_expanded,
    .v-sidebar-menu.vsm_collapsed {
        position: relative;
        height: 100%;
        background: transparent linear-gradient(180deg, $colorGradientBlueDark 0%, $colorGradientBlueLight 100%) 0% 0% no-repeat padding-box;

        .vsm--title {
            display: inline-block;
            width: 80%;
            word-wrap: break-word;
            white-space: normal;
        }

        .vsm--icon {
            width: 16px;
            margin-left: 8px;
            margin-right: 16px;
        }

        .vsm--link_hover {
            background: #08b8d1;
            opacity: 1;
        }

        .vsm--toggle-btn {
            background: transparent 0% 0% no-repeat padding-box;

            .vsm--toggle-btn_slot {
                background: transparent 0% 0% no-repeat padding-box;
            }
        }

        .vsm--dropdown > .vsm--list {
            background: transparent 0% 0% no-repeat padding-box;
        }

        .vsm--item_open {
            // background: $colorConfirm 0% 0% no-repeat padding-box;
            .vsm--link_level-1 {
                background: $colorConfirm 0% 0% no-repeat padding-box;
                color: $colorButtonMultiAdd;
                z-index: 10;
            }
        }

        .vsm--item {
            .vsm--link {
                outline: none;

                .vsm--icon {
                    background-color: #ffffff00;
                }
            }
        }

        .vsm--link_exact-active,
        .vsm--link_active {
            font-weight: 600;
            background: $colorButtonMultiAdd 0% 0% no-repeat padding-box;
            color: $colorWhite;
        }
    }

    .vsm--link_level-2 {
        padding-left: 3.35rem;
        margin-left: -0.5rem;
        width: 105%;

        .vsm--title {
            display: inline-block;
            width: 80%;
            word-wrap: break-word;
            white-space: normal;
        }
    }
}
