.table-box {
    padding: 30px;
    // 在html中 保留原始空白與換行
    white-space: pre;
    overflow-x: auto;

    table.table-wrap {
        width: 100%;
        padding-left: 21px;
        opacity: 1;
        text-align: center;

        tr:hover {
            background: #f8f8f8;
        }

        th {
            font-size: 0.9rem;
            height: 49px;
            font-weight: Bold;
            color: $colorTextDark;
            opacity: 1;
            padding: 10px;
            vertical-align: center;
            user-select: none;
            border-top: 2px #cecece solid;
            border-bottom: 2px #cecece solid;

            &:first-child {
                text-align: center;

                width: 6%;
                margin-bottom: 0;

                label {
                    margin-bottom: 0;
                }
            }

            &:last-child {
                width: 8%;
            }

            &.sortable {
                cursor: pointer;
            }
        }

        td {
            font-size: 0.9rem;
            height: 49px;
            color: $colorTextDark;
            opacity: 1;
            padding-top: 3px;
            padding-bottom: 3px;
            border-top: 1px #cecece solid;
            border-bottom: 1px #cecece solid;

            &:first-child {
                padding: 0;

                width: 6%;

                label {
                    margin-bottom: 0;
                }
            }

            &:nth-child(2) {
                padding: 0;
                width: 3%;
            }

            &:last-child {
                width: 8%;
            }

            &.action-container {
                padding-left: 10px;
                padding-right: 10px;

                // text-align: left;
            }

            .action-box {
                width: 35px;
                height: 35px;
                margin-left: 2px;
                margin-right: 2px;
                padding-right: 8px;
                color: $colorTextDark;
                font-size: 1.2rem;
                border: 1px solid rgba(255, 255, 255, 0);
                border-radius: 50%;
                cursor: pointer;

                &:hover {
                    color: $colorBlack;
                    opacity: 1;
                }

                &:active {
                    border: 1px solid $colorTextDark;
                }
            }
        }
    }

    .pagination-container {
        margin-top: 20px;
        margin-bottom: 10px;
        height: 30px;
        width: inherit;
        opacity: 1;
        text-align: left;

        .btn-primary.disabled,
        .btn-primary:disabled {
            color: $colorWhite;
            background-color: $colorConfirm;
            border-color: $colorConfirm;
        }

        .row {
            height: 30px;

            .col {
                height: 30px;
            }
        }

        .pagination-text {
            font-weight: Regular;
            color: $colorTextDark;
            opacity: 1;
            margin: 0 10px;
        }

        .pagination-group {
            .pagination-control {
                width: 63px;
                height: 30px;
                background: $colorWhite 0% 0% no-repeat padding-box;
                border: 1px solid #b9b9b9;
                border-radius: 6px;
                opacity: 1;
            }

            .pagination-assign {
                width: 90px;
                height: 30px;
                background: $colorWhite 0% 0% no-repeat padding-box;
                border: 1px solid #b9b9b9;
                border-radius: 6px;
                padding-left: 6px;
                opacity: 1;
            }
        }

        .pagination-box {
            display: inline-flex;
            padding: 0px;

            .pagination-button-first-last {
                margin-left: 5px;

                &:hover {
                    background-color: #dadcde;
                    border-color: #b5b8bd;
                    color: $colorBlack;
                }

                &:disabled {
                    color: $colorBlack;
                    background-color: $colorWhite;
                    border-color: #6c757d;
                }
            }

            .pagination-button {
                margin-left: 5px;

                &:hover {
                    background-color: #dadcde;
                    border-color: #b5b8bd;
                    color: $colorBlack;
                }

                &:disabled {
                    color: $colorWhite;
                    background-color: $colorConfirm;
                    border-color: $colorConfirm;
                }
            }
        }
    }

    input[type='checkbox'] {
        width: 1rem;
        height: 1rem;
    }

    ul li {
        list-style-type: none;
        text-align: left;
    }

    .table-photo {
        height: 80px;
        margin: 3px;
    }
}
