// toolbox dropdown
.more-wrap {
    &:hover {
        cursor: pointer;
    }

    .clear-dropdown-style {
        font-style: inherit;
        color: inherit;
        background-color: transparent;
        font-size: inherit;
        text-decoration: none;
        font-variant: inherit;
        font-weight: bold;
        line-height: inherit;
        font-family: inherit;
        border-radius: inherit;
        border: inherit;
        outline: inherit;
        box-shadow: inherit;
        // padding: inherit;
        vertical-align: inherit;
    }

    .dropdown.b-dropdown.btn-group {
        .btn.dropdown-toggle.btn-link.btn-lg.text-decoration-none.dropdown-toggle-no-caret.focus,
        .btn.dropdown-toggle.btn-link.btn-lg.text-decoration-none.dropdown-toggle-no-caret:focus {
            outline: 0;
            box-shadow: none;
            text-decoration: none;
            color: #000 !important;
        }
    }

    .dropdown-menu {
        margin-top: -0.2rem;
        // table 置中
        // margin-right: 5.5rem;
    }
}

// toolbox more options
.toolbox-more-option {
    color: black;
    padding: 6px 20px;

    &:hover {
        background: #cecece;
        cursor: pointer;
    }

    .toolbox-item {
        outline: none;
        border: none;
    }

    .option-icon {
        margin-right: 15px;
    }
}

// toolbox small item
.action-box {
    outline: none;
    border: none;

    .option-icon {
        outline: none;
        border: none;
    }

    &:hover {
        cursor: pointer;
        opacity: 0.5;
    }
}

.table-box table.table-wrap td .action-box:active {
    border: none;
}
