.container-box {
    position: relative;
    border-radius: 6px;

    img {
        border-radius: 6px;
    }
}

.form-image {
    background-color: $colorWhite;
    border-radius: 8px;
    width: 150px;
    height: 150px;
    cursor: pointer;
}

// 始圖片可以在img裡面適應大小不變形
.contain {
    object-fit: contain;
    cursor: pointer;
}

.contain-default {
    object-fit: contain;
}

.up-img {
    position: absolute;
    background-color: $colorBlack;
    border-radius: 8px;
    width: 150px;
    height: 150px;
    top: 0;
    opacity: 0.5;
}

.btn-upload {
    position: absolute;
    background-color: $colorDelete;
    color: $colorWhite;
    border: none;
    top: 38px;
    left: 20px;
    z-index: 2;
    padding: 0.15rem 1rem;

    &:hover {
        background-color: $colorDelete;
        opacity: 0.8;
    }
}

.btn-download {
    position: absolute;
    background-color: #b9b9b9;
    color: $colorWhite;
    border: none;
    top: 38px;
    left: 84px;
    z-index: 2;
    padding: 0.15rem 1rem;

    &:hover {
        background-color: #b9b9b9;
        opacity: 0.8;
    }
}

.btn-close {
    position: absolute;
    background-color: $colorRed;
    color: $colorWhite;
    border: none;
    top: 86px;
    left: 20px;
    z-index: 2;
    padding: 0.15rem 1.1rem 0.15rem 1.1rem;

    &:hover {
        background-color: $colorRed;
        opacity: 0.8;
    }
}

.btn-view {
    position: absolute;
    background-color: $colorYellow;
    color: $colorWhite;
    border: none;
    top: 86px;
    left: 84px;
    z-index: 2;
    padding: 0.15rem 1rem;

    &:hover {
        background-color: $colorYellow;
        opacity: 0.8;
    }
}

.btn-upload-only {
    position: absolute;
    background-color: $colorDelete;
    color: $colorWhite;
    border: none;
    top: 60px;
    left: 50px;
    z-index: 2;
    padding: 0.15rem 1rem;

    &:hover {
        background-color: $colorDelete;
        opacity: 0.8;
    }
}

.crop-button {
    margin: 10px 0;
    padding: 10px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow-y: hidden;
    border-radius: 3px;
    padding-bottom: 20px;

    .content {
        background-color: $colorWhite;
        width: 100%;
        height: 100%;
        border-radius: 6px;
        padding: 10px;
        overflow-y: scroll;
        overflow: hidden;
        display: flex;
        padding: 10px 0;
        flex-direction: column;

        //  1.標題及圖像說明
        .content-desc {
            margin: 0px 10px;

            .title {
                font-size: 16px;
                border-left: 5px solid #2d8cf0;
                padding-left: 10px;
                margin-bottom: 16px;
            }

            .desc {
                margin-left: 18px;
            }
        }

        // 2.圖像區域
        .content-image {
            display: flex;
            justify-content: rows;
            margin: 20px 30px;

            li {
                width: 180px;
                height: 180px;
                margin-right: 20px;
                position: relative;

                .delete-img {
                    display: none;
                }

                &:hover {
                    .delete-img {
                        display: block;
                        position: absolute;
                        width: 180px;
                        height: 40px;
                        line-height: 40px;
                        left: 0px;
                        top: 140px;
                        background: rgba(59, 60, 61, 0.5);
                        // box-sizing: content-box;
                        z-index: 999;
                        cursor: pointer;
                        text-align: right;

                        i {
                            margin: 8px 10px 0 0;
                            display: block;
                            font-size: 24px;
                            color: $colorWhite;
                        }
                    }
                }
            }

            img {
                width: 180px;
                height: 180px;
                border-radius: 4px;
            }

            .upload-img:hover {
                border: 1px dashed #92cccc;
            }
        }
    }

    // 裁剪圖像對話框

    .img-crop {
        // 1.截圖區域
        .imgCrop-content {
            margin: 0 20px;
            display: flex;
            display: -webkit-flex;
            justify-content: flex-start;
            -webkit-justify-content: flex-start;

            .cropper-content {
                width: 40rem;
                height: 40rem;
                // margin-right: 40px;
            }

            .preview-content {
                .show-preview {
                    // border: 1px solid #ccc;
                    box-sizing: border-box;
                    width: 150px;
                    height: 150px;
                    overflow: hidden;
                    flex: 1;
                    -webkit-flex: 1;
                    display: flex;
                    justify-content: flex-start;
                    justify-content: center;
                    -webkit-justify-content: center;

                    .preview {
                        overflow: hidden;
                        /*border-radius: 50%;*/
                        /*border: 1px solid #cccccc;*/
                        background: $colorWhite;
                        margin-left: 0px;
                    }
                }

                .desc {
                    margin-top: 10px;
                    text-align: center;
                }

                .el-button {
                    margin: 10px 40px;
                }
            }
        }

        // 2.操作按鈕區域
        .btn-content {
            width: 40rem;
            // margin: 10px 20px;
            text-align: center;
            margin: 1rem 2rem;

            // #uploads {
            //   position: absolute;
            //   clip: rect(0 0 0 0);
            // }
            .btn {
                // height: 2rem;
                // width: 2rem;
                // font-size: 1.5rem;
                margin: 3px 5px;
                // background-color: $colorWhite;
                border: 1px solid #999;
                // border-radius: 4px;
                padding-top: 8px;
            }
        }
    }
}

.drop-zone input {
    position: absolute;
    // cursor: pointer;
    top: 0px;
    right: 0;
    bottom: 0;
    left: 0;
    width: inherit;
    height: inherit;
    opacity: 0;
}

.drop-zone-dropping {
    position: absolute;
    border: 2px dashed #2e94c4;
    top: 0;
    width: inherit;
    height: inherit;
    border-radius: inherit;
}

