.mx-datepicker {
    .mx-input {
        min-height: 38px !important;
        line-height: 38px !important;
        font-size: 16px !important;

        &::placeholder {
            font-size: 14px !important;
        }
    }

    // 選擇之後 改變 border 的顏色
    &.selected {
        .mx-input {
            border-color: $colorSuccess !important;
        }
    }

    .mx-input:disabled {
        cursor: default;
        color: $colorBlack;
    }
}
