body {
    overflow: hidden;
    font-family: 'Century Gothic', 'Microsoft JhengHei';
    outline: none;
    color: $coloPrimaryBlackText;
    letter-spacing: 0.3px;
}

/* width */
::-webkit-scrollbar {
    width: 0.6rem;
}

::-webkit-scrollbar {
    height: 0.6rem;
}

/* Track */
::-webkit-scrollbar-track {
    border-radius: 0.6rem;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 0.6rem;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #ddd;
}

