//////////////////////////// customize form ////////////////////////////

.form-control {
    line-height: 38px;
    &::placeholder {
        font-size: 14px !important;
    }
}

.customize-form-error {
    margin-left: 0.2rem;
    margin-top: 0.3rem;
    color: #e26a6a;
}

.customize-form-disabled {
    background-color: $colorFormDisabled !important;
    border: 1px solid $colorFormDisabledBorder !important;
}

.element-disabled {
    border-radius: 5px;
    background-color: $colorFormDisabled;
    border: 1px solid $colorFormDisabledBorder;
    text-align: left;
    min-height: 35px;
    padding: 7px 12px 6px 12px;
}

.form-label {
    font-weight: bold;
}

.required-star-label {
    color: $colorRed;
}

.form-display-none {
    display: none;
}

.form-multi-header {
    margin-top: 1rem;
    margin-bottom: 0.5rem;
}

.form-multi-row {
    margin-top: 0.4rem;
}

//////////////////////////// strong password tip ////////////////////////////
// strong password tip
.tip-title {
    color: $colorRed;
    font-weight: bold;
    margin-right: 0.4rem;
}

.tip {
    color: #ffd700;
    font-weight: bold;
    margin-right: 0.4rem;
}

.tip-border {
    padding: 1rem;
    border: 2px dashed $colorBlack;
    border-radius: 0.5rem;
    margin-bottom: 1.5rem;
    margin-top: 0.5rem;
    width: 40rem;
}

//////////////////////////// Button ////////////////////////////
.button-group-center {
    display: flex;
    justify-content: center;
    margin-top: 1.5rem;
    margin-bottom: 2rem;
}

.button-group-right {
    float: right;
    margin: 0rem 2rem 1.5rem 2rem;

    &.button-group-right-form {
        margin-top: 1.6rem;
    }

    .button-footer {
        min-width: 6.5rem;
        font-size: 1.2rem;
        padding-top: 0.45rem;
        padding-bottom: 0.6rem;
    }
}

// remove focus around button click
.btn:focus {
    outline: none !important;
    box-shadow: none !important;
}

.button-confirm {
    background-color: $colorConfirm;
    border: 1px solid $colorConfirm;
    opacity: 0.8;

    &:hover {
        background-color: $colorConfirm;
        border: 1px solid $colorConfirm;
        opacity: 1;
    }

    &:active,
    &:focus,
    &:visited {
        background-color: $colorConfirm;
        border: 1px solid $colorConfirm;
        opacity: 1;

        &:hover {
            background-color: $colorConfirm;
            border: 1px solid $colorConfirm;
            opacity: 0.8;
        }
    }

    &:disabled {
        background-color: $colorConfirm;
        border: 1px solid $colorConfirm;
        /* button disabled remove hover */
        pointer-events: none;
        opacity: 0.5;
    }
}

.button-delete {
    background-color: $colorDelete;
    border: 1px solid $colorDelete;
    opacity: 0.8;

    &:hover {
        background-color: $colorDelete;
        border: 1px solid $colorDelete;
        opacity: 1;
    }

    &:active,
    &:focus,
    &:visited {
        background-color: $colorDelete;
        border: 1px solid $colorDelete;
        opacity: 1;

        &:hover {
            background-color: $colorDelete;
            border: 1px solid $colorDelete;
            opacity: 0.8;
        }
    }

    &:disabled {
        background-color: $colorDelete;
        border: 1px solid $colorDelete;
        /* button disabled remove hover */
        pointer-events: none;
        opacity: 0.35;
    }
}

.button-warning {
    background-color: $colorWarning;
    border: 1px solid $colorWarning;
    outline: 0;
    opacity: 0.8;
    color: $colorBlack;

    &:hover {
        background-color: $colorWarning;
        border: 1px solid $colorWarning;
        opacity: 1;
        color: $colorBlack;
    }

    &:active,
    &:focus,
    &:visited {
        background-color: $colorWarning;
        border: 1px solid $colorWarning;
        opacity: 1;

        &:hover {
            background-color: $colorWarning;
            border: 1px solid $colorWarning;
            outline: 0;
            opacity: 0.8;
            color: $colorBlack;
        }
    }

    &:disabled {
        background-color: $colorWarning;
        border: 1px solid $colorWarning;
        color: $colorBlack;
        /* button disabled remove hover */
        pointer-events: none;
        opacity: 0.5;
    }
}

.button-success {
    background-color: $colorSuccess;
    border: 1px solid $colorSuccess;
    opacity: 0.8;

    &:hover {
        background-color: $colorSuccess;
        border: 1px solid $colorSuccess;
