.button-cancel {
    background-color: $colorWhite;
    border: none;
    color: $colorConfirm;
}

.button-cancel-transparent {
    background-color: transparent;
    border: none;
    color: $colorConfirm;
}

.button-enroll-template-excel {
    background-color: $colorDelete;
    color: $colorWhite;
    border: none;
}

.button-enroll-template-csv {
    background-color: #27ce00;
    color: $colorWhite;
    border: none;
}

.button-download-excel {
    padding: 0.5rem 1rem;
}

//////////////////////////// Header ////////////////////////////
.form-header {
    font-size: 2.2rem;
    font-weight: bold;
    margin-bottom: 1.5rem;
    margin-top: -0.5rem;
}

.arrow-back {
    margin-left: 0.5rem;
    font-size: 2rem;

    &:hover {
        cursor: pointer;
    }
}

//////////////////////////// status color ////////////////////////////
.success {
    color: $colorSuccess;
}

.error {
    color: $colorRed;
}

.error-bold {
    color: $colorRed;
    font-weight: bold;
}

//////////////////////////// form multiple create or edit ////////////////////////////
.form-multi-header {
    margin-top: 1rem;
    margin-bottom: 0.5rem;
}

