.vue-form-wizard.md {
    .wizard-navigation .wizard-progress-with-circle {
        position: relative;
        top: 50px;
        height: 4px;
    }
    .checked {
        outline: none;
    }
    .wizard-icon-circle {
        width: 90px;
        height: 90px;
        font-weight: Bold;
        letter-spacing: 0.4px;
        opacity: 1;
        .wizard-icon {
            font-weight: Bold;
            letter-spacing: 0.4px;
            opacity: 1;
        }
        .wizard-icon-container {
            border-radius: 42%;
        }
    }
    .wizard-nav-pills {
        li.active > a .wizard-icon {
            font-weight: Bold;
            letter-spacing: 0.4px;
            opacity: 1;
        }
    }
}
.stepTitle {
    margin-top: 20px;
    font-weight: Bold;
    letter-spacing: 0.16px;
    opacity: 1;
}
