@import "~sweetalert2/dist/sweetalert2.min.css";

.popup-class {
    padding: 0;
    width: 26rem;
}

.swal2-header {
    align-items: flex-start !important;
}

.header-class {
    background-color: $colorPopupsHeader;
    font-size: 10px;
    text-align: left;
    padding: 1rem 0.3rem 0.3rem 0.3rem;
    border-radius: 6px 6px 0 0;
}

.title-class {
    float: left !important;
    padding-left: 1.8rem;
}

.content-class {
    padding: 0.8rem 0.8rem 0.2rem 1rem;
}

.confirm-button-class {
    margin: 0.2rem 1rem 2rem 1rem;
}

.cancel-button-class {
    margin: 0.2rem 1rem 2rem 1rem;
    color: $colorConfirm !important;
}