$colorMultiBorder: #ced4da;

.multiselect {
    min-height: 35px;
    font-size: 14px;

    &__placeholder {
        margin: 3px 4px;
        color: #7d858c;
    }

    &__single {
        font-size: 16px;
        margin-bottom: 1.5px;
        padding-top: -5px;
        padding-bottom: 6px;
    }

    &__placeholder {
        padding-top: 4px;
    }

    &__select {
        margin-top: 35x;
    }

    &__select:before {
        top: 55%;
    }

    // li.multiselect__element {
    //     width: inherit;
    // }

    // select input
    &__tags {
        min-height: 38px;
        line-height: 8px;
        font-size: 14px;
        // border 的 default 顏色
        border: 1px solid $colorMultiBorder;
    }

    // 選擇之後 改變 border 的顏色
    &.selected {
        .multiselect__tags {
            min-height: 35px;
            line-height: 9px;
            font-size: 14px;
            border-color: $colorSuccess;
        }
    }

    .multiselect__tag {
        background: $colorWhite;
        color: $colorBlack;
        border: 1px solid $colorMultiBorder;
        margin-top: -2px;

        .multiselect__tag-icon {
            background: $colorWhite;
            color: $colorBlack;
        }

        .multiselect__tag-icon:after {
            background: $colorWhite;
            color: $colorBlack;
        }
    }

    &.multiselect--disabled {
        height: 34px;
        min-height: 35px;
        border-radius: 5px;
        border: 0px solid #aaa;
        background: $colorFormDisabled;
        color: $colorBlack;
        opacity: 1;

        .multiselect__select {
            height: 32px;
            background: $colorFormDisabled;
        }

        .multiselect__tags {
            height: 34px;
            min-height: 35px;
            line-height: 10px;
            font-size: 14px;
            background: $colorFormDisabled;
            margin-bottom: 6px;
            padding-bottom: 3px;
            opacity: 0.6;

            .multiselect__single {
                background: $colorFormDisabled;
            }
        }
    }

    // selected background

    .multiselect__option {
        &--selected {
            background: $colorFormDisabledBorder;

            // > &--highlight {
            //     background: #337ab7;
            // }
        }

        &--selected:after {
            color: $colorWhite;
            font-weight: bold;
        }

        &--disabled {
            background: #7d858c !important;
        }

        &--group,
        &--group:after {
            background: #99e9f5 !important;
            color: #269ab1 !important;
        }

        &--highlight {
            background: #337ab7;
        }
    }

    // when slelectd and mouse move in slelectd option
    .multiselect__option--selected.multiselect__option--highlight {
        background: #337ab7;
    }

    .multiselect__option--selected.multiselect__option--highlight:after {
        background: #337ab7;
        margin-right: 3px;
    }
}
